@import "var";

/* ########################################
      Global style for every page on the website */
/* */


html, body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-size: 16px;
  font-family: "Segoe UI", sans-serif;
  color: $fontColor;
  overflow-x: hidden;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../images/background.png");
    opacity: .018;
    z-index: -10; }

  .align {
    text-align: center;
    margin: 0; }

  .header {
    width: 100vw;

    .upperSection {
      height: 45px;
      padding: 25px 40px;

      #mailMe img {
        height: 150px;
        margin: 7.5px 0; }

      .languageSelect {
        float: right;

        #flagDE, #flagUK {
          width: auto;
          height: 25px;
          margin: 10px 10px;
          cursor: pointer;
          box-shadow: 1px 3px 10px 0px $fontColor; }

        #flagDE.picked, #flagUK.picked {
          transform: scale(1.2); }

        .flagSlash {
          display: inline-block;
          background-color: $fontColor;
          width: 3px;
          height: 45px;
          transform: rotate(7deg); } } }

    #titleHeader {
      text-align: center;
      font-size: 2.5rem;
      margin: 0 15px;
      color: $fontColor;

      .name {
        font-weight: 600; } }

    #typed {
      font-size: 2.5rem;
      font-weight: 700;
      color: #354858;
      overflow-wrap: break-word; }

    .typed-cursor {
      font-size: 2.5rem; } }


  .overviewMenu {
    position: absolute;
    margin: 0 auto;
    bottom: 0;
    height: calc( 100% - 95px );
    width: 100%;
    overflow-y: hidden;
    left: 50%;
    transform: translateX(-50%);

    .menuTree {
      width: 100%;
      height: 100%;
      bottom: -5px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);

      #ScreenBackground {
        max-height: 100%;
        width: 100%;
        max-width: 90%;
        left: 50%;
        transform: translateX(-50%);
        bottom: -5px;
        opacity: .6;
        position: absolute; }

      nav {
        position: absolute;
        width: 100%;
        height: 100%;

        a {
          display: block;
          width: 11rem;
          height: 11rem;
          transition: 0.5s;
          text-decoration: none;
          color: $fontColor;

          img {
            width: 11rem;
            height: 11rem; }

          &:hover {
            cursor: pointer;
            transform: translateY(-10px); } }

        #menuGithub, h3 {
          position: absolute;
          bottom: 20%;
          left: 10%; }

        #menuResume, h3 {
          position: absolute;
          bottom: 50%;
          left: 30%; }

        #menuPhotography, h3 {
          position: absolute;
          bottom: 40%;
          left: 50%;
          transform: translateX(-50%); }

        #menuPhotography {
          display: none; }

        #menuPhotography:hover {
            transform: translate(-50%, -10px); }

        #menuProjects, h3 {
          position: absolute;
          bottom: 50%;
          right: 30%; }

        #menuLinkedIn, h3 {
          position: absolute;
          bottom: 20%;
          right: 10%; }

        h3 {
          transform: translate(-50%, 75%);
          text-align: center; }

        #menuPhotography h3 {
          transform: translate(-75%, -15%); }

        #menuTitle_cv {
          width: 100%; } } } }

  #background {
    display: none;
    width: 100%;
    bottom: 0;
    top: 0;
    position: inherit;
    background-color: rgba(#222, .5);
    pointer-events: none; }

  .popUpSite {
    display: none;
    position: absolute;
    width: 52%;
    max-width: 1300px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    min-height: 92vh;
    margin: 35px 50px;
    border-radius: 0 0 5px 5px;
    box-shadow: 1px 3px 10px 0px $fontColor;
    background-color: white;
    overflow-x: hidden;

    .topicHeader {
      width: 100%;
      height: 75px;
      background-color: rgba(#68C3DE, .78);
      border-radius: 0 0 8px 8px;

      .topicTitle {
        width: 90%;
        height: 100%;
        float: left;
        font-size: 2rem;
        text-align: center;
        margin: 0;
        padding: 15px 15px 15px calc(15px + 10%);
        box-sizing: border-box; }

      .r-col {
        width: 10%;
        height: 100%;
        float: right;

        &:hover {
          cursor: pointer; }

        #closeSubPage {
          width: 35px;
          height: 35px;
          margin: 10px auto;
          padding: 10px;
          z-index: 100; }

        .closePopUpSite {
          position: absolute;
          float: right;
          height: 30px;
          width: 2px;
          top: 22px;
          right: 37px;
          background-color: $fontColor;

          &:nth-child(1) {
            transform: rotate(45deg); }

          &:nth-child(2) {
            transform: rotate(-45deg); } } } }

    #resume {
      background-color: rgba($resumeColor, .78); }

    #projects {
      background-color: rgba($projectColor, .78); }

    #photography {
      background-color: rgba($photographyColor, .78); }

    .project {
      margin: 1.5rem;
      position: relative;

      .projectImg {
        width: 30%;
        height: auto; }

      .description {
        position: absolute;
        display: inline-block;
        margin: auto 2rem;
        box-sizing: border-box;
        width: calc(70% - 2rem);

        .linkButtons {
          float: right; }

        .tag {
          pointer-events: none; }

        .linkTag, .tag {
          display: inline-block;
          border-radius: 3px;
          margin: 0.3rem 0.2rem;
          padding: 0.2rem 0.5rem;
          text-decoration: none;
          color: $fontColor;
          background: $secondBackground;


          .linkImg {
            height: 1rem;
            margin: auto 0.3rem auto 0; } }

        .linkTag {
          display: inline-flex;

          &:hover {
            background-color: darken( $secondBackground, 5% ); } } } }

    .siteContent {
      display: contents;
      padding: 10px;
      box-sizing: border-box;

      #cv {
        position: absolute;
        top: 75px;
        left: 0;
        width: 100%;
        height: calc( 100% - 78px ); } }

    .siteContentPhoto {
      padding: 100px 140px;

      .photographyGrid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-areas: ". . ." ". . ." ". . .";
        grid-gap: 120px;
        margin: 15px auto;

        .photography_imageItem {
          position: relative;
          background-color: #F9F9F9;
          animation: fadeInWobble 0.8s ease-out normal backwards;

          img {
            position: absolute;
            width: 100%;
            height: 100%; }

          &::before {
            content: "";
            display: inline-block;
            width: 1px;
            height: 0;
            padding-bottom: 100%; }

          &:hover {
            transition: all 0.6s ease-in-out;
            transform: scale(1.15); } }


        @for $i1 from 1 through 3 {
          .imageItem#{$i1} { animation-delay: 200ms * $i1; }; }

        @for $i2 from 4 through 6 {
          .imageItem#{$i2} { animation-delay: 200ms * ( $i2 - 3 ) + 200ms; }; }

        @for $i3 from 7 through 9 {
          .imageItem#{$i3} { animation-delay: 200ms * ( $i3 - 6 ) + 400ms; }; } } } }

  .popUpSiteOpen {
    display: grid; }

  .big {
    width: 80vw; } }

@keyframes fadeIn {
  from {
    transform: scale(0); }

  to {
    transform: scale(1); } }

@keyframes fadeInWobble {
  0% {
    transform: scale(0); }

  70% {
    transform: scale(1.06); }

  100% {
    transform: scale(1); } }

body:fullscreen #background {
  display: block;
  background-color: #fff;
  z-index: -10;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../images/background.png");
    opacity: .03;
    z-index: -10; } }

body:fullscreen {
  overflow: scroll !important; }

body:-ms-fullscreen {
  overflow: scroll !important; }

body:-webkit-full-screen {
  overflow: scroll !important; }

body:-moz-full-screen {
  overflow: scroll !important; }

/* ########################################
      Media Queries */
/* */

@media only screen and (max-width: 1025px) {

  body {
    font-size: 12px;

    .menuTree nav {
      height: 90% !important;
      bottom: 0;

      a, img {
        width: 8rem !important;
        height: 8rem !important; } } }

  #ScreenBackground {
    object-fit: cover;
    height: 85%;
    max-width: 100% !important; }

  .popUpSite {
    width: calc( 100vw - 10px ) !important;
    margin: 35px 5px !important;
    left: 0 !important;
    transform: translateX(0) !important; } }

@media only screen and (max-width: 500px) {

  body {
    font-size: 11px;

    .menuTree nav {
      height: 80% !important;
      bottom: 0;

      a, img {
        width: 8rem !important;
        height: 8rem !important; }

      #menuResume {
        left: 10% !important; }

      //#menuPhotography
      //  right: 10% !important

      #menuProjects {
        right: 10% !important; } }

    #ScreenBackground {
      object-fit: cover;
      height: 85%; }

    .popUpSite {
      width: calc( 100vw - 10px ) !important;
      margin: 35px 5px; } } }

@media only screen and (max-height: 500px) {

  body {
    font-size: 12px;

    .header {

      .align span {
        font-size: 25px !important; }

      .align {
        position: absolute;
        top: 0;
        margin: 20px auto;
        left: 50%;
        transform: translateX(-50%); } }

    #ScreenBackground {
      object-fit: cover;
      height: 85%; }

    .popUpSite {
      width: calc( 100vw - 10px ) !important;
      margin: 35px 5px; } } }

@media only screen and (min-width: 1921px) {

  html, body {
    font-size: 35px;

    #ScreenBackground {
      max-width: 90%; }

    .topicTitle {
      font-size: 40px !important; } } }
